.newHomeContent {
  height: 100vh;
  width: 100%;
  position: relative;

  .newHeader {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 10000;

    .logoPage {
      display: inline-block;
      width: 180px;

      img {
        width: 100%;
      }
    }

    .navbar {
      padding: 10px 0;
      background: none !important;
    }

    .rightMenu {
      a {
        color: #adadad;
        font-weight: bold;
        font-size: 18px;
        padding: 9px 10px 8px;
        margin-left: 30px;

        &:hover {
          color: rgb(101, 221, 221);
          cursor: pointer;
          text-decoration: none;
        }
      }
    }
  }

  .carousel {
    .carousel-indicators {
      li {
        margin: 0 10px;
        height: 4px;
        background-color: #d0d0d0;
      }
    }

    .carousel-inner {
      height: 100vh;

      .carousel-item {
        height: 100vh;

        img {
          height: 100%;
          width: 100%;
        }
      }
    }
  }

  .sliderImage {
    height: 100%;
    width: 100%;
  }
}

@media(max-width: 768px) {

  .newHomeContent {
    .carousel {
      .carousel-indicators {
        li {
          margin: 0 8px;
          width: 20px;
        }
      }
    }
  }

  .newHeader {
    .navbar {
      .navbar-collapse {
        .rightMenu {
          padding: 10px 24px;
          background: #0c0c0cb0;

          a {
            margin-left: 0;
          }
        }
      }
    }
  }
}
